<template>
  <div class="data row">
    <section class="col-md-6">
      <h5>Consumption</h5>
      <line-chart :chart-data="line_datacollection" :options="lineChartOptions"></line-chart>
    </section>
    <section class="data col-md-6">
      <section class="row">
        <h5 class="col-md-6">Line Observation</h5> <button class="col-md-4">Switch Line Off</button>
      </section>
      <bubble-chart :chart-data="bubble_datacollection" :options="bubbleChartOptions"></bubble-chart>
    </section>
  </div>
</template>

<script>
import LineChart from '../components/LineChart.js'
import BubbleChart from '../components/BubbleChart.js'

export default {
  components: { LineChart, BubbleChart },
  name: 'Monitor',
  mounted () {
    this.fillLineData()
    this.fillBubbleData()
    this.getLiveReadings()
    this.getRandomReadings()
  },
  data () {
    return {
      msg: 'Live Monitor',
      line_datacollection: null,
      bubble_datacollection: null,
      lineChartOptions: {responsive: true, maintainAspectRatio: false},
      bubbleChartOptions: {
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [{
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: false
        },
        tooltips: {
          enabled: true,
          mode: 'single',
          callbacks: {
            label: function (tooltipItems, data) {
              return tooltipItems.yLabel
            }
          }
        },
        responsive: true,
        maintainAspectRatio: false,
        height: 200
      },
      source1: [0, 0, 0, 0, 0, 0, 0, 0],
      source2: [0, 0, 0, 0, 0, 0, 0, 0],
      source3: [0, 0, 0, 0, 0, 0, 0, 0],
      source4: [
        {
          x: 100,
          y: 0,
          r: 5
        },
        {
          x: 60,
          y: 30,
          r: 5
        },
        {
          x: 40,
          y: 60,
          r: 5
        },
        {
          x: 80,
          y: 80,
          r: 5
        },
        {
          x: 20,
          y: 30,
          r: 5
        },
        {
          x: 0,
          y: 100,
          r: 5
        }
      ],
      source5: [
        {
          x: 100,
          y: 0,
          r: 5
        },
        {
          x: 60,
          y: 30,
          r: 5
        },
        {
          x: 40,
          y: 60,
          r: 5
        },
        {
          x: 80,
          y: 80,
          r: 5
        },
        {
          x: 20,
          y: 30,
          r: 5
        },
        {
          x: 0,
          y: 100,
          r: 5
        }
      ],
      source6: [
        {
          x: 100,
          y: 0,
          r: 5
        },
        {
          x: 60,
          y: 30,
          r: 5
        },
        {
          x: 40,
          y: 60,
          r: 5
        },
        {
          x: 80,
          y: 80,
          r: 5
        },
        {
          x: 20,
          y: 30,
          r: 5
        },
        {
          x: 0,
          y: 100,
          r: 5
        }
      ]
    }
  },
  methods: {
    fillLineData: function () {
      this.line_datacollection = {
        labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
        datasets: [
          {
            label: 'Live m1',
            borderColor: '#01FF00',
            pointBackgroundColor: 'white',
            borderWidth: 2,
            pointBorderColor: '#01FF00',
            backgroundColor: 'transparent',
            data: this.source1
          },
          {
            label: 'Emulated m2',
            borderColor: '#249EBF',
            pointBackgroundColor: 'white',
            borderWidth: 1,
            pointBorderColor: '#249EBF',
            backgroundColor: 'transparent',
            data: this.source2
          },
          {
            label: 'Emulated m3',
            borderColor: '#FF0E00',
            pointBackgroundColor: 'white',
            borderWidth: 1,
            pointBorderColor: '#FF0E00',
            backgroundColor: 'transparent',
            data: this.source3
          }
        ]
      }
    },
    fillBubbleData: function () {
      this.bubble_datacollection = {
        labels: ['Danger'],
        datasets: [
          {
            label: 'Live Monitor lm1',
            backgroundColor: '#01FF00',
            pointBackgroundColor: 'white',
            borderWidth: 1,
            pointBorderColor: '#01FF00',
            data: this.source4
          },
          {
            label: 'Simulated Monitor Sm2',
            backgroundColor: '#FF0E00',
            pointBackgroundColor: '#FF0E00',
            borderWidth: 1,
            pointBorderColor: '#FF0E00',
            data: this.source5
          },
          {
            label: 'Simulated Monitor Sm3',
            backgroundColor: '#249EBF',
            pointBackgroundColor: '#249EBF',
            borderWidth: 1,
            pointBorderColor: '#249EBF',
            data: this.source6
          }
        ]
      }
    },
    getRandomReadings: function () {
      setInterval(() => {
        this.fillLineData()
        this.fillBubbleData()
        console.log('Init :' + this.line_datacollection.datasets[0].data)
        pushRandomData(this.source2)
        pushRandomData(this.source3)
        pushRandomGroupData(this.source4)
        pushRandomGroupData(this.source5)
        console.log('Even :' + this.line_datacollection.datasets[0].data)
      }, 1000)
    },
    getLiveReadings: function () {
      const lfill = this.fillLineData
      var lsource1 = this.source1
      var ws = new WebSocket('wss://' + location.host)
      ws.onopen = function () {
        console.log('Successfully connect WebSocket')
      }
      ws.onmessage = function (message) {
        console.log('receive message' + message.data)
        try {
          var obj = JSON.parse(message.data)
          if (!obj.liveReading) {
            return
          }
          lfill()
          pushData(lsource1, obj.liveReading)
        } catch (err) {
          console.error(err)
        }
      }
    }
  }
}

function pushRandomData (array) {
  array.push(r())
  array.shift()
}

function pushData (array, data) {
  array.push(data)
  array.shift()
}

function pushRandomGroupData (array) {
  var point = { x: r(), y: r(), r: rRange(5) }
  array.push(point)
  array.shift()
}

function r () {
  return Math.floor(Math.random() * 1000) + 1
}

function rRange (range) {
  return Math.floor(Math.random() * range) + 1
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}

</style>



